/** Media Queries **/
.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}